/* SCSS Variables
---------------------------------------------- */
$bodycolor: #222;
$lightgrey: #f1f1f1;
$fontmono: 'Roboto Mono', "Courier New", Courier, arial, helvetica, monospace;
$fontbold: 'Roboto','HK Grotesk',arial, helvetica, sans-serif;
$fontregular: 'Roboto','webregular','HK Grotesk','Roboto', arial, helvetica, sans-serif;
$accent-color: black;
$s-space: 0.75rem;
$m-space: 1.2rem;
$l-space: 2.5rem;
$xl-space: 5rem;
$theborder: 0.2rem solid $bodycolor;
$theborder-white: 0.2rem solid white;


/* CSS Reset
---------------------------------------------- */
a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
 margin: 0;
 padding: 0;
 border: 0;
 font: inherit;
 vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
 display: block;
}

body {
 line-height: 1;
}

ol,
ul {
 list-style: none;
}

blockquote,
q {
 quotes: none;
}

blockquote:after,
blockquote:before,
q:after,
q:before {
 content: '';
 content: none;
}

table {
 border-collapse: collapse;
 border-spacing: 0;
}
/* remove blue hover on Chrome */
button:focus {
 outline: 0;
}
